import React, { useState } from 'react';
import { useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import { Doughnut } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import logo from '../images/logo.png';
import moment, { locale } from 'moment';
import Header from './Header';

function Pacientes() {
  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';

  const changeState = useDispatch();
  const viewPacientes = useSelector((state) => state.pacientes);
  const [cleanrender, setcleanrender] = useState(0);

  // recuperando estados atualizados.
  const idusuario = useSelector((state) => state.idusuario);
  const usuario = useSelector((state) => state.nomeusuario);
  const funcao = useSelector((state) => state.funcao);
  const tipo = useSelector((state) => state.tipo);
  const hospital = useSelector((state) => state.nomehospital);
  const unidade = useSelector((state) => state.nomeunidade);
  const tipounidade = useSelector((state) => state.tipounidade);

  // carregamento do número de leitos do cti selecionado.
  const [leitos, setLeitos] = useState(10);
  const loadLeitos = () => {
    // ROTA: SELECT * FROM hospitaisxunidades WHERE hospital = hospital AND unidade = unidade.
    axios.get(html + "/totalleitosunidade/'" + hospital + "'/'" + unidade + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      const arrayLeitos = x.map((item) => item.leitos);
      setLeitos(arrayLeitos[0]);
    });
  }

  // lista de pacientes.
  const [listpacientes, setlistpacientes] = useState([]);
  const loadPacientes = () => {
    axios.get(html + "/pacientes").then((response) => {
      var x = [0, 1];
      x = response.data;
      setlistpacientes(x);
    });
  }

  // tela processando.
  function WaitPlease() {
    if (showwaitplease === 1) {
      return (
        <div style={{
          position: 'absolute', top: 0, bottom: 0, left: 0, right: 0, backgroundColor: '#000000', opacity: 0.5,
          display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center',
          zIndex: 9, margin: 0, padding: 0,
        }}>
          <img alt="" src={logo} style={{ height: 100, width: 100 }}></img>
          <label className="title2" style={{ color: '#ffffff' }}>CARREGANDO...</label>
        </div>
      )
    } else {
      return null;
    }
  }

  // lista de atendimentos.
  const [showwaitplease, setshowwaitplease] = useState(0);
  const [listatendimentos, setlistatendimentos] = useState([]);
  const loadAtendimentos = () => {
    setlistatendimentos([0, 1]);
    setshowwaitplease(1);
    axios.get(html + "/atendimentos").then((response) => {
      var x = [0, 1];
      x = response.data;
      setlistatendimentos(x.filter((item) => item.ativo !== 0 && item.hospital === hospital && item.unidade === unidade));
      setshowwaitplease(0);
    });
  }

  // renderização da lista de pacientes.
  function ShowPacientes() {
    if (listatendimentos.length > 0) {
      return (
        <div
          className="scroll"
          id="LISTA DE PACIENTES"
          style={{
            display: 'flex',
            justifyContent: 'flex-start',
            borderRadius: 5,
            margin: 5,
            minHeight: 0.70 * window.innerHeight,
            height: 0.70 * window.innerHeight,
          }}
        >
          {atendimentos.map((item) => (
            <div
              key={item.id}
              className="row"
              style={{
                position: 'relative',
                margin: 0,
              }}
            >
              <button
                className="widget"
                style={{ minWidth: 50, margin: 2.5, color: '#ffffff', backgroundColor: 'grey' }}
                title="BOX"
                disabled="true"
              >
                {item.box}
              </button>
              <button
                onClick={() => selectPaciente(item)}
                className={item.status == 3 ? 'green-button' : item.status == 2 ? 'yellow-button' : item.status == 1 ? 'red-button' : item.status == 0 ? 'grey-button' : 'pool-button'}
                title={
                  'STATUS: ' +
                  item.status +
                  '. CLIQUE PARA EVOLUIR.'
                }
                style={{
                  margin: 2.5,
                  width: '70%',
                }}
              >
                {listpacientes.filter((value) => value.id === item.idpaciente).map((item) => item.nome)}
              </button>
              <button
                className="blue-button"
                style={{
                  display: window.innerWidth < 800 ? 'none' : 'flex',
                  margin: 2.5,
                  width: '20%',
                  backgroundColor: item.status == 3 ? '#52be80' : item.status == 2 ? '#f5b041' : item.status == 1 ? 'rgba(236, 112, 99, 0.8)' : item.status == 0 ? '#CACFD2' : '#AED6F1',
                }}
              >
                {moment().diff(moment(listpacientes.filter((value) => value.id === item.idpaciente).map((item) => item.dn), 'DD/MM/YYYY'), 'years') + ' ANOS.'}
              </button>
              <button
                className="blue-button"
                style={{
                  margin: 2.5,
                  width: '30%',
                  backgroundColor: item.status == 3 ? '#52be80' : item.status == 2 ? '#f5b041' : item.status == 1 ? 'rgba(236, 112, 99, 0.8)' : item.status == 0 ? '#CACFD2' : '#AED6F1',
                }}
              >
                {moment().diff(moment(item.admissao, 'DD/MM/YYYY'), 'days') + ' DIAS.'}
              </button>
              <button
                className="blue-button"
                style={{
                  display: window.innerWidth < 800 ? 'none' : 'flex',
                  margin: 2.5,
                  width: '30%',
                  backgroundColor: item.status == 3 ? '#52be80' : item.status == 2 ? '#f5b041' : item.status == 1 ? 'rgba(236, 112, 99, 0.8)' : item.status == 0 ? '#CACFD2' : '#AED6F1',
                }}
              >
                {item.assistente}
              </button>
            </div>
          ))}
        </div>
      );
    } else {
      return (
        <div
          className="scroll"
          id="LISTA DE PACIENTES"
          style={{
            display: 'flex',
            justifyContent: 'flex-start',
            borderRadius: 5,
            margin: 5,
            marginLeft: 5,
            minHeight: '70vh',
            height: '70vh',
          }}
        >
          <p className="title2"
            style={{
              fontSize: 22,
              fontWeight: 'bold',
              margin: 10,
              padding: 0,
            }}>
            {'NÃO HÁ PACIENTES INTERNADOS NESTA UNIDADE.'}
          </p>
        </div>
      )
    }
  }

  // carregamento da lista de pacientes para a unidade selecionada, perfil PRONTO-ATENDIMENTO.
  const [atendimentos, setatendimentos] = useState([]);
  const loadPacientesPa = () => {
    // todos os atendimentos não classificados, de qualquer unidade/setor (classificação 0).
    axios.get(html + '/atendimentos').then((response) => {
      var x = [0, 1];
      x = response.data;
      var y = x.filter((item) => item.hospital === hospital && item.unidade === unidade && item.ativo !== 0);
      setatendimentos(y);
    });
  }

  // renderização da lista de pacientes, perfil PRONTO-ATENDIMENTO.
  function ShowPacientesPa() {
    if (atendimentos.length > 0) {
      return (
        <div
          className="scroll"
          id="LISTA DE PACIENTES"
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'flex-start',
            borderRadius: 5,
            margin: 5,
            marginBottom: 15,
            minHeight: window.innerHeight - 195,
            height: window.innerHeight - 195,
          }}
        >
          {atendimentos.sort(((a, b) => a.classificacao > b.classificacao ? 1 : -1)).map((item) => (
            <div
              key={item.id}
              className="row"
              style={{
                position: 'relative',
                margin: 0,
                opacity: item.classificacao === 0 ? 0.5 : 1,
              }}
            >
              <button
                className="blue-button"
                title="HORA DE ENTRADA."
                style={{
                  margin: 2.5, backgroundColor: '#1f7a8c',
                  width: window.innerWidth > 800 ? 160 : 110,
                  padding: 5,
                }}
                disabled="true"
              >
                {item.admissao}
              </button>
              <button
                className="blue-button"
                title="TEMPO DE ESPERA."
                style={{
                  display: window.innerWidth > 800 ? 'flex' : 'none',
                  justifyContent: 'center',
                  margin: 2.5, padding: 10, backgroundColor: '#1f7a8c',
                  width: 250,
                }}
                disabled="true"
              >
                <div>{moment().diff(moment(item.admissao, 'DD/MM/YYYY HH:mm'), 'hours') + 'H'}</div>
                <div style={{ margin: 2.5 }}>{' : '}</div>
                <div>{moment().diff(moment(item.admissao, 'DD/MM/YYYY HH:mm'), 'minutes') - (moment().diff(moment(item.admissao, 'DD/MM/YYYY HH:mm'), 'hours') * 60) + 'MIN'}
                </div>
              </button>
              <button
                className="blue-button"
                title="BOX/LEITO."
                style={{ minWidth: 50, width: 50, margin: 2.5, backgroundColor: '#1f7a8c' }}
              >
                {item.box}
              </button>
              <button
                onClick={() => selectPaciente(item)}
                disabled={item.classificacao === 0 ? true : false}
                title={item.classificacao === 0 ? 'PACIENTE AGUARDA TRIAGEM, NÃO É POSSÍVEL ATENDER.' : 'DESCRITOR: ' + item.descritor + '. CLIQUE PARA ATENDER.'}
                className={
                  item.classificacao === 1 ? "red-button" :
                    item.classificacao === 2 ? "orange-button" :
                      item.classificacao === 3 ? "yellow-button" :
                        item.classificacao === 4 ? "green-button" : "blue-button"
                }
                style={{
                  margin: 2.5,
                  width: '100%',
                }}
              >
                {item.nome}
              </button>
              <button
                className="blue-button"
                style={{
                  margin: 2.5, backgroundColor: '#1f7a8c',
                  minWidth: window.innerWidth > 800 ? 100 : 50,
                  width: window.innerWidth > 800 ? 100 : 50,
                }}
              >
                {moment().diff(moment(item.dn, 'DD/MM/YYYY'), 'years') + ' ANOS'}
              </button>
            </div>
          ))}
        </div>
      );
    } else {
      return (
        <div
          className="scroll"
          id="LISTA DE PACIENTES"
          style={{
            display: 'flex',
            justifyContent: 'flex-start',
            borderRadius: 5,
            margin: 5,
            marginLeft: 0,
            minHeight: '70vh',
            height: '70vh',
          }}
        >
          <div className="title2"
            style={{
              fontSize: 22,
              fontWeight: 'bold',
              margin: 10,
              padding: 0,
            }}>
            {'NÃO HÁ PACIENTES INTERNADOS NÃO CLASSIFICADOS NESTA UNIDADE.'}
          </div>
        </div>
      )
    }
  }

  // cabeçalhos das listas.
  function ShowPacientesHeader() {
    return (
      <div
        className="row"
        style={{
          width: '100%', paddingLeft: 10, marginTop: 5,
          paddingRight: window.innerWidth > 800 ? 35 : 35
        }}
      >
        <button
          className="header"
          style={{
            width: 50,
            margin: 2.5,
          }}
          title="BOX"
          disabled="true"
        >
          BOX
        </button>
        <button
          className="header"
          style={{
            margin: 2.5,
            width: '70%',
          }}
        >
          NOME
        </button>
        <button
          className="header"
          style={{
            display: window.innerWidth < 800 ? 'none' : 'flex',
            margin: 2.5,
            width: '20%',
          }}
        >
          IDADE
        </button>
        <button
          className="header"
          style={{
            margin: 2.5,
            width: '30%',
          }}
        >
          TEMPO DE INTERNAÇÃO
        </button>
        <button
          className="header"
          style={{
            display: window.innerWidth < 800 ? 'none' : 'flex',
            margin: 2.5,
            width: '30%',
          }}
        >
          MÉDICO ASSISTENTE
        </button>
      </div >
    );
  }

  function ShowPacientesHeaderPa() {
    return (
      <div
        className="row"
        style={{
          width: '100%',
          paddingLeft: window.innerWidth > 800 ? 15 : 10,
          paddingRight: window.innerWidth > 800 ? 55 : 35
        }}
      >
        <button
          className="header"
          style={{
            padding: 5,
            width: window.innerWidth > 800 ? 160 : 110,
            margin: 2.5
          }}
          disabled="true"
        >
          ENTRADA
        </button>
        <button
          className="header"
          style={{
            width: 230, margin: 2.5, padding: 10,
            display: window.innerWidth > 800 ? 'flex' : 'none',
          }}
        >
          TEMPO DE ESPERA
        </button>
        <button
          className="header"
          style={{
            minWidth: 50, width: 50, margin: 2.5,
          }}
        >
          BOX
        </button>
        <button
          className="header"
          style={{ width: '100%', margin: 2.5 }}
        >
          NOME
        </button>
        <button
          className="header"
          style={{
            width: window.innerWidth > 800 ? 100 : 50,
            margin: 2.5
          }}
        >
          IDADE
        </button>
      </div >
    );
  }

  // CHART.
  /* gráfico em torta que exibe o total de pacientes internados na unidade, distribuídos
  por gravidade. */
  const dataChart = {
    labels: [' INSTÁVEIS', ' EM ALERTA', ' ESTÁVEIS', ' CONFORTO', ' INDEFINIDO', ' VAGOS'],
    datasets: [
      {
        data: [
          listatendimentos.filter((item) => item.status === 1).length,
          listatendimentos.filter((item) => item.status === 2).length,
          listatendimentos.filter((item) => item.status === 3).length,
          listatendimentos.filter((item) => item.status === 0).length,
          listatendimentos.filter((item) => item.status === 4).length,
          leitos - (
            listatendimentos.filter((item) => item.status === 1).length +
            listatendimentos.filter((item) => item.status === 2).length +
            listatendimentos.filter((item) => item.status === 3).length +
            listatendimentos.filter((item) => item.status === 0).length +
            listatendimentos.filter((item) => item.status === 4).length)
        ],
        backgroundColor: [
          '#ec7063', // vermelho.
          '#f5b041', // amarelo.
          '#52be80', // verde.
          '#BDC3C7', // conforto.
          '#aed6f1', // indefinido.
          'rgba(153,163,164,1)', // vago.
        ],
        borderColor: '#F1F3F9',
        hoverBorderColor: ['#F1F3F9', '#F1F3F9', '#F1F3F9', '#F1F3F9'],
      },
    ],
  };
  function Chart() {
    if (listatendimentos.length > 0) {
      return (
        <div
          id="GRÁFICO"
          className="secondary"
          style={{
            display: 'flex',
            flexDirection: window.innerWidth > 800 ? 'column' : window.innerWidth > 600 ? 'row' : 'column',
            justifyContent: 'space-around',
            alignItems: 'center',
            backgroundColor: '#f1f3f9',
            borderColor: '#f1f3f9',
            borderRadius: 5,
            padding: 30,
            margin: 5,
            marginTop: 7.5,
          }}
        >
          <div id="chart">
            <Doughnut
              data={dataChart}
              width={window.innerWidth < 800 ? 180 : 250}
              height={window.innerWidth < 800 ? 180 : 250}
              plugins={ChartDataLabels}
              options={{
                plugins: {
                  datalabels: {
                    display: function (context) {
                      return context.dataset.data[context.dataIndex] !== 0;
                    },
                    color: '#FFFFFF',
                    font: {
                      weight: 'bold',
                      size: 16,
                    },
                  },
                },
                tooltips: {
                  enabled: false,
                },
                hover: { mode: null },
                elements: {
                  arc: {
                    borderColor: '#F1F3F9',
                    borderWidth: 5,
                  },
                },
                animation: {
                  duration: 0,
                },
                title: {
                  display: false,
                  text: 'STATUS DOS PACIENTES:',
                },
                legend: {
                  display: false,
                  position: 'bottom',
                },
                maintainAspectRatio: true,
                responsive: false,
              }}
            ></Doughnut>
            <div>
              <p
                className="title2"
                style={{
                  fontSize: 16,
                  fontWeight: 'bold',
                  margin: 2.5,
                  marginTop: 10,
                  padding: 0,
                }}
              >
                {'OCUPAÇÃO: ' + Math.ceil(listatendimentos.length * 100 / leitos) + '%'}
              </p>
            </div>
          </div>
          <Legenda></Legenda>
        </div>
      );
    } else {
      return null;
    }
  }

  // legenda para o gráfico.
  function Legenda() {
    return (
      <div
        className="secondary"
        style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', padding: 5, margin: 5 }}
      >
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', width: '100%', alignItems: 'center' }}>
          <div
            id="ESTÁVEIS"
            style={{
              width: 20,
              height: 20,
              borderRadius: 5,
              backgroundColor: '#52be80',
              margin: 2.5,
              padding: 0,
            }}
          ></div>
          <p
            className="title2"
            style={{ fontSize: 10, margin: 2.5, marginRight: 5, padding: 0, textAlign: 'center', }}
          >
            ESTÁVEIS
        </p>
          <div
            id="EM ALERTA"
            style={{
              width: 20,
              height: 20,
              borderRadius: 5,
              backgroundColor: '#f7dc6f',
              margin: 2.5,
              padding: 0,
            }}
          ></div>
          <p
            className="title2"
            style={{ fontSize: 10, margin: 2.5, marginRight: 5, padding: 0, textAlign: 'center' }}
          >
            {window.innerWidth < 800 ? 'ALERTA' : 'EM ALERTA'}
          </p>
          <div
            id="INSTÁVEIS"
            style={{
              width: 20,
              height: 20,
              borderRadius: 5,
              backgroundColor: '#ec7063',
              margin: 2.5,
              padding: 0,
            }}
          ></div>
          <p
            className="title2"
            style={{ fontSize: 10, margin: 2.5, marginRight: 5, padding: 0, textAlign: 'center' }}
          >
            INSTÁVEIS
        </p>
        </div>
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center', width: '100%' }}>
          <div
            id="CONFORTO"
            style={{
              width: 20,
              height: 20,
              borderRadius: 5,
              backgroundColor: '#BDC3C7',
              margin: 2.5,
              padding: 0,
            }}
          ></div>
          <p
            className="title2"
            style={{ fontSize: 10, margin: 2.5, marginRight: 5, padding: 0, textAlign: 'center' }}
          >
            CONFORTO
        </p>
          <div
            id="INDEFINIDOS"
            style={{
              width: 20,
              height: 20,
              borderRadius: 5,
              backgroundColor: '#AED6F1',
              margin: 2.5,
              padding: 0,
            }}
          ></div>
          <p
            className="title2"
            style={{ fontSize: 10, margin: 2.5, marginRight: 5, padding: 0, textAlign: 'center' }}
          >
            {window.innerWidth < 800 ? 'INDEF.' : 'INDEFINIDOS'}
          </p>
          <div
            id="VAGOS"
            style={{
              width: 20,
              height: 20,
              borderRadius: 5,
              backgroundColor: 'rgba(153,163,164,1)',
              margin: 2.5,
              padding: 0,
            }}
          ></div>
          <p
            className="title2"
            style={{ fontSize: 10, margin: 2.5, marginRight: 5, padding: 0, textAlign: 'center' }}
          >
            VAGOS
        </p>
        </div>
      </div>
    );
  }

  // selecionando um paciente da lista e abrindo a tela corrida.
  const selectPaciente = (item) => {
    changeState({
      type: 'CORRIDA_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadFuncao: funcao,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
      payloadNomeunidade: unidade,
      payloadIdpaciente: item.idpaciente,
      payloadIdatendimento: item.id,
    });
  };

  // definindo a visão de componentes (INTERNAÇÃO X PRONTO-ATENDIMENTO).
  const [modolista, setmodolista] = useState(1);
  const [preparedchart, setpreparedchart] = useState(0);

  useEffect(() => {
    if (viewPacientes === 1) {
      // carregando a lista de pacientes.
      loadPacientes();
      loadAtendimentos();
      // definindo o modo de exibição da lista de pacientes (INTERNAÇÃO X PRONTO-ATENDIMENTO).
      defineTipoUnidade();
      setcleanrender(0);
      // carregndo o total de leitos da unidade.
      loadLeitos();
      // carregando a lista de pacientes do PRONTO-ATENDIMENTO.
      loadPacientesPa();
      // renderizando a tela após a construção "tempestuosa" dos gráficos.
      setTimeout(() => {
        setcleanrender(1);
      }, 1000);
      setTimeout(() => {
        setpreparedchart(1);
      }, 2000);
    }
  }, [viewPacientes]);

  // definindo tipo de unidade (INTERNAÇÃO X PRONTO-ATENDIMENTO).
  let y = 0;
  const defineTipoUnidade = () => {
    axios.get(html + "/totalleitos/'" + hospital + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      y = x.filter((item) => item.hospital === hospital && item.unidade === unidade).map((item) => item.pa);
      setmodolista(x.filter((item) => item.hospital === hospital && item.unidade === unidade).map((item) => item.pa));
    });
  }

  // renderização do componente.
  if (viewPacientes === 1 && cleanrender === 1) {
    return (
      <div
        className="main fade-in"
        style={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'flex-start',
          overflowY: window.innerWidth > 800 ? 'hidden' : 'scroll',
          overflowX: 'hidden',
          width: '100%',
          margin: 0,
          padding: 0,
        }}
      >
        <Header></Header>
        <div
          id="PRINCIPAL"
          style={{
            display: modolista == 0 ? 'flex' : 'none',
            flexDirection: window.innerWidth < 800 ? 'column' : 'row',
            justifyContent: 'center',
            width: '100%',
            paddingTop: 2.5
          }}>
          <Chart></Chart>
          <WaitPlease></WaitPlease>
          <div
            style={{
              display: modolista == 0 ? 'flex' : 'none',
              flexDirection: 'column',
              width: '100%',
            }}>
            <ShowPacientesHeader></ShowPacientesHeader>
            <ShowPacientes></ShowPacientes>
          </div>
        </div>
        <div
          style={{
            display: modolista == 1 ? 'flex' : 'none',
            flexDirection: 'column',
            justifyContent: 'center',
            width: '100%',
          }}>
          <ShowPacientesHeaderPa></ShowPacientesHeaderPa>
          <ShowPacientesPa></ShowPacientesPa>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Pacientes;
