import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import logo from '../images/logo.png';
import logoff from '../images/power.svg';
import back from '../images/back.svg';
import foto from '../images/3x4.jpg';

function Header() {
  // estados das telas:
  const changeState = useDispatch();
  const hospitais = useSelector((state) => state.hospitais);
  const unidades = useSelector((state) => state.unidades);
  const pacientes = useSelector((state) => state.pacientes);
  const farmacia = useSelector((state) => state.farmacia);
  const escala = useSelector((state) => state.escala);
  const corrida = useSelector((state) => state.corrida);
  const secretaria = useSelector((state) => state.secretaria);
  const usuarios = useSelector((state) => state.usuarios);
  const atendimentos = useSelector((state) => state.atendimentos);
  const acolhimento = useSelector((state) => state.acolhimento);
  // estados do usuário:
  const idusuario = useSelector((state) => state.idusuario);
  const nomeusuario = useSelector((state) => state.nomeusuario);
  const funcao = useSelector((state) => state.funcao);
  const tipo = useSelector((state) => state.tipo);
  // estados referentes ao hospital e unidade:
  const idhospital = useSelector((state) => state.idhospital);
  const nomehospital = useSelector((state) => state.nomehospital);
  const idunidade = useSelector((state) => state.idunidade);
  const nomeunidade = useSelector((state) => state.nomeunidade);
  const tipounidade = useSelector((state) => state.tipounidade);

  const clickVoltar = () => {
    if (unidades === 1 || acolhimento === 1) {
      changeState({
        type: 'HOSPITAL_ON',
        // payloads.
        payloadIdusuario: idusuario,
        payloadNomeusuario: nomeusuario,
        payloadTipo: tipo,
      });
    } else if (atendimentos === 1) {
      changeState({
        type: 'SECRETARIA_ON',
        // payloads.
        payloadIdusuario: idusuario,
        payloadNomeusuario: nomeusuario,
        payloadTipo: tipo,
      });
    } else if (hospitais === 1 && tipo === 3) {
      changeState({
        type: 'SECRETARIA_ON',
        // payloads.
        payloadIdusuario: idusuario,
        payloadNomeusuario: nomeusuario,
        payloadTipo: tipo,
      });
    } else if (hospitais === 1 && tipo !== 3) {
      changeState({ type: 'LOGIN_ON' });
    } else if (corrida === 1) {
      changeState({
        type: 'PACIENTES_ON',
        // payloads.
        payloadIdusuario: idusuario,
        payloadNomeusuario: nomeusuario,
        payloadFuncao: funcao,
        payloadTipo: tipo,
        payloadTipounidade: tipounidade,
        payloadNomehospital: nomehospital,
        payloadNomeunidade: nomeunidade,
      });
    } else if (escala === 1 || pacientes === 1 || farmacia === 1 || acolhimento === 1) {
      changeState({
        type: 'UNIDADE_ON',
        // payloads.
        payloadIdusuario: idusuario,
        payloadNomeusuario: nomeusuario,
        payloadFuncao: funcao,
        payloadTipo: tipo,
        payloadNomehospital: nomehospital,
      });
    } else if (usuarios === 1) {
      changeState({
        type: 'ESCALA_ON',
        // payloads.
        payloadIdusuario: idusuario,
        payloadNomeusuario: nomeusuario,
        payloadTipo: tipo,
        payloadIdhospital: idhospital,
        payloadNomehospital: nomehospital,
        payloadIdunidade: idunidade,
        payloadNomeunidade: nomeunidade,
      });
    } else {
      changeState({ type: 'LOGIN_ON' });
    }
  };

  const clickLogoff = () => {
    changeState({ type: 'LOGIN_ON' });
  };

  // renderização do componente.
  return (
    <div
      id="HEADER"
      style={{
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        margin: 0,
        padding: 0,
        width: '100%',
        height: 0.2 * window.innerHeight,
      }}
    >
      <div
        className="secondary"
        style={{
          display: corrida === 1 || window.innerWidth < 800 ? 'none' : 'flex',
          backgroundColor: '#E1E5F2',
          borderRadius: 5,
          height: 0.2 * window.innerHeight,
          width: 100,
          minWidth: 100,
          maxWidth: 100,
        }}
      >
        <img
          alt=""
          id="logo"
          src={logo}
          style={{
            margin: 0,
            padding: 10,
            height: 0.15 * window.innerHeight,
          }}
        ></img>
      </div>
      <img
        alt=""
        id="logo"
        src={foto}
        style={{
          display: corrida === 1 ? 'flex' : 'none',
          margin: 0,
          marginLeft: 5,
          marginRight: 5,
          padding: 0,
          height: 0.2 * window.innerHeight,
          borderRadius: 5,
        }}
      ></img>
      <div
        className="secondary"
        style={{
          display: window.innerWidth < 800 && corrida == 1 ? 'none' :  'flex',
          backgroundColor: '#E1E5F2',
          borderRadius: 5,
          width: '100%',
          height: 0.2 * window.innerHeight,
        }}
      >
        <p className="title1" style={{ color: '#1f7a8c', fontSize: window.innerWidth < 800 ? 18 : 22, height: 0.2 * window.innerHeight, padding: 5 }}>
          {hospitais === 1 ? 'HOSPITAIS' :
            unidades === 1 ? 'UNIDADES: ' + nomehospital :
              pacientes === 1 ? 'PACIENTES: ' + nomeunidade :
                farmacia === 1 ? 'DISPENSAÇÃO DE MEDICAMENTOS: ' + nomeunidade :
                  escala === 1 ? 'ESCALA DE MÉDICOS: ' + nomeunidade :
                    corrida === 1 ? 'PRONTUÁRIO' :
                      usuarios === 1 ? 'GERENCIAMENTO DE PLANTONISTAS' :
                        secretaria === 1 ? 'PAINEL ADMINISTRATIVO' :
                          atendimentos === 1 ? 'ATENDIMENTOS' :
                            acolhimento === 1 ? 'ACOLHIMENTO: ' + nomeunidade : null
          }
        </p>
      </div>
      <div
        className="secondary"
        style={{
          display: 'flex',
          flexDirection: 'column',
          backgroundColor: '#E1E5F2',
          borderRadius: 5,
          height: 0.2 * window.innerHeight,
          width: window.innerWidth < 800 ? '100%' : '20%',
          margin: 5,
          padding: 5,
        }}
      >
        <div className="title2" style={{ fontSize: 14, padding: 5, width: 0.3 * window.innerWidth, margin: 2.5 }}>{'OLÁ, ' + nomeusuario}</div>
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
          <button
            className="blue-button"
            title="VOLTAR."
            onClick={() => clickVoltar()}
            disabled={hospitais === 1 && tipo !== 3 ? true : false}
            style={{
              opacity: hospitais === 1 && tipo !== 3 ? 0.3 : 1,
              width: 50,
              height: 50,
              margin: 2.5,
              padding: 20,
            }}
          >
            <img
              alt=""
              src={back}
              style={{
                margin: 5,
                height: 25,
                width: 25,
              }}
            ></img>
          </button>
          <button
            className="blue-button"
            title="FAZER LOGOFF."
            onClick={() => clickLogoff()}
            style={{
              display: 'flex',
              width: 50,
              height: 50,
              margin: 2.5,
              padding: 20,
            }}
          >
            <img
              alt=""
              src={logoff}
              style={{
                margin: 5,
                height: 25,
                width: 25,
              }}
            ></img>
          </button>
        </div>
      </div>
    </div>
  );
}

export default Header;